import * as THREE from "three"
import { extend } from "@react-three/fiber"

export class ImageFadeMaterial extends THREE.ShaderMaterial {
  constructor() {
    super({
      uniforms: {
        effectFactor: { value: 1.2 },
        dispFactor: { value: 0 },
        tex: { value: undefined },
        tex2: { value: undefined },
        disp: { value: undefined },
        time: { get value() { return 0.0002 * (Date.now() % 30000) } }
        //itime: {value: Date.now()}
      },
      vertexShader: `varying vec2 vUv;
      void main() {
        vUv = uv;
        gl_Position = projectionMatrix * modelViewMatrix * vec4( position, 0.3 );
      }`,
      fragmentShader: `varying vec2 vUv;
      uniform sampler2D tex;
      uniform sampler2D tex2;
      uniform sampler2D disp;
      uniform float _rot;
      uniform float dispFactor;
      uniform float effectFactor;
      uniform float time;
      #define PI 3.14159265358979323846

      vec2 rotate2D(vec2 _st, float _angle){
          _st -= 0.5;
          _st =  mat2(cos(_angle),-sin(_angle),
                      sin(_angle),cos(_angle)) * _st;
          _st += 0.5;
          return _st;
      }
      
      vec2 tile(vec2 _st, float _zoom){
          _st *= _zoom;
          return fract(_st);
      }
      
      float box(vec2 _st, vec2 _size, float _smoothEdges){
          _size = vec2(0.5)-_size*0.5;
          vec2 aa = vec2(_smoothEdges*0.5);
          vec2 uv = smoothstep(_size,_size+aa,_st);
          uv *= smoothstep(_size,_size+aa,vec2(1.0)-_st);
          return uv.x*uv.y;
      }

      void main() {
        vec2 uv = vUv;

        // Time varying pixel color
        //vec3 col = 0.5 + 0.5*cos(time+uv.xyx+vec3(0,2,4));
        vec2 st = vUv;
        vec3 color = vec3(0.0);

        // Divide the space in 4
        st = tile(st,10.);

        // Use a matrix to rotate the space 45 degrees
        st = rotate2D(st,PI*0.25);

        // Draw a square
        color = vec3(box(st,vec2(0.5),0.01));
        // color = vec3(st,0.0);

        gl_FragColor = vec4(color,1.0);
        // Output to screen
        //fragColor = vec4(col,1.0);
        // vec4 disp = texture2D(disp, uv);
        // vec2 distortedPosition = vec2(uv.x, uv.y + dispFactor * (disp.r*effectFactor));
        // vec2 distortedPosition2 = vec2(uv.x, uv.y - (1.0 - dispFactor) * (disp.r*effectFactor));
        // vec4 _texture = texture2D(tex, distortedPosition);
        // vec4 _texture2 = texture2D(tex2, distortedPosition2);
        // vec4 finalTexture = mix(_texture, _texture2, dispFactor);
        //gl_FragColor = vec4(1.0);
      }`
    })
  }

  get effectFactor() {
    return this.uniforms.effectFactor.value
  }
  set effectFactor(v) {
    return (this.uniforms.effectFactor.value = v)
  }
  get dispFactor() {
    return this.uniforms.dispFactor.value
  }
  set dispFactor(v) {
    return (this.uniforms.dispFactor.value = v)
  }
  get tex() {
    return this.uniforms.tex.value
  }
  set tex(v) {
    return (this.uniforms.tex.value = v)
  }
  get tex2() {
    return this.uniforms.tex2.value
  }
  set tex2(v) {
    return (this.uniforms.tex2.value = v)
  }
  get disp() {
    return this.uniforms.disp.value
  }
  set disp(v) {
    return (this.uniforms.disp.value = v)
  }

  // get time() {
  //   return this.uniforms.time.value
  // }
  // set itime(v) {
  //   return (this.uniforms.itime.value = Date.now())
  // }
}

extend({ ImageFadeMaterial })

//export default ImageFadeMaterial;